.c-btn::after {
  right: 1.5em;
  border-style: solid;
  border-width: 0 2px 2px 0;
  content: "";
  display: inline-block;
  position: absolute;
  height: 9px;
  width: 9px;
  border-color: #a90e0e;
  transform: rotate(-45deg);
}
.c-btn:hover {
  transform: translate3d(0, 5px, 0);
  color: #a90e0e;
  box-shadow: none;
  background: #fcee21;
  transition: 0.5s ease;
}
.c-btn--item {
  width: 40%;
