}
.c-btn--all {
  width: 40%;
  margin: auto;
}
@media screen and (max-width: 1024px) {
  .c-btn--all {
    width: 100%;
  }
}
.c-btn--attention {
  background: #fcee21;
  overflow: hidden;
}
.c-btn--attention:before {
  content: "";
  position: absolute;
  top: -100px;
  left: -100px;
  width: 50px;
  height: 50px;
  background-image: linear-gradient(100deg, rgba(255, 255, 255, 0) 10%, rgb(255, 255, 255) 100%, rgba(255, 255, 255, 0) 0%);
